@font-face {
  font-family: 'Product Sans';
  font-style: normal;
  font-weight: 100;
  src: url(https://fonts.gstatic.com/s/productsans/v5/HYvgU2fE2nRJvZ5JFAumwegdm0LZdjqr5-oayXSOefg.woff2) format('woff2');
}

* {
  padding-left: 0.5vw;
  cursor: none;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: none;
}

img {
  margin-top: 3vh;
  height: 70px;
  width: 70px;
}

#nyu:hover {
  color: purple;
}

#avatar {
  position: absolute;
  right: 0px;
  bottom: 0px;
  height: 40vh;
  width: 40vh;
  z-index: -2;
}

h1 {
  user-select: none;
  font-family: "Product Sans", sans-serif;
  font-size: 5vw;
  color: white;
  margin: 4vh 0 2vh 0;
}

h2 {
  user-select: none;
  font-family: "Product Sans", sans-serif;
  font-size: 4vw;
  color: grey;
  margin: 0 0 1vh 0;
}

h2:hover{
  color: white;
}

body {
  height: 100%;
  overflow: hidden;
  background-color: #000000;
}

@media screen and (max-width: 840px) {
  h1 {
    user-select: none;
    font-family: "Product Sans", sans-serif;
    font-size: 12vw;
    color: white;
    margin: 4vh 0 2vh 0;
  }

  h2 {
    user-select: none;
    font-family: "Product Sans", sans-serif;
    font-size: 10vw;
    color: grey;
    margin: 0.2vh 0;
  }

  img {
    max-height: 10vw;
    max-width: 10vw;
  }

  #avatar {
    display: none;
  }
}

